import "../../scss/pages/home-about.scss";
import Global from "../common/global";

(() => {

    let aboutApi = "/api/Home/About";
    Global.showLoading();
    $.ajax({
        type: "get",
        url: aboutApi,
        success: (msg: any) => {
            if (msg.Code === 0) {
                $("#about").html(msg.Data);
            }
        },
        complete: () => {
            Global.hideLoading();
        }
    })
})();